<style lang="scss" scoped>
    .address{
        width: 100%;
        height: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: column;
    }
    .content{
        width: 100%;
        flex: 1;
        overflow: hidden;
        margin-top: 40px;
        .bscroll-container{
            width: 100%;
            height: auto;
        }
    }
    .foot{
            width: 100%;
            height: 50px;
            background: #1B1A1F;
            display: flex;
            justify-content: center;
            align-items: center;
            .btn{
                width: 80%;
                height: 30px;
                background: #fff;
                border-radius: 10px;
                color: #1B1A1F;
            }
        }
</style>
<template>
    <div class="address">
        <Head title="地址列表"/>
        <div class="content" ref="bscroll">
            <div class="bscroll-container">
                
            </div>
        </div>
        <div class="foot">
            <div class="btn" @click="changeurl">增加地址</div>
        </div>
    </div>
</template>
<script>
import BScroll from "better-scroll"
import Head from '@/components/Head.vue'
export default {
    data(){
        return{
            addressarr:[]
        }
    },
    components: {
    Head,
  },
  mounted(){
      console.log(this.addressarr)
      this.$nextTick(()=>{
          this.bscroll=new BScroll(this.$refs.bscroll,{
           click:true,
          probeType:2
        })
      })
      if(this.$route.params.datas){
          console.log(this.$route.params.datas)
          this.addressarr.push(this.$route.params.datas)
          console.log(this.addressarr)
      }
  },
  methods:{
      changeurl(){
          this.$router.push({
              name:"addaddress"
          })
      }
  }
}
</script>

