<template>
    <div class="store-page">
        <div class="store-tab">
            <span :class="list?'tab-active':''" @click="tabList">店铺列表</span>
            <span :class="me?'tab-active':''" @click="tabMe">我的店铺</span>
        </div>
        <div class="store-list">
            <List v-show="list" @scrollmsg="scrollmsg"/>
            <Me v-show="me"/>
        </div>
    </div>
</template>

<script>
import List from "./store-list.vue";
import Me from "./Mystore.vue";
export default {
  name: "store",
  components: {
    List,
    Me
  },
  data() {
    return {
      list: 1,
      me: 0,
      scroll: null
    };
  },
  //   beforeRouteLeave(to, from, next) {
  //     let positiony = this.scroll.y;
  //     console.log(positiony);
  //     if (positiony == null) positiony = 0;
  //     this.$store.commit("changeScrollY3", positiony);
  //     next();
  //   },
  methods: {
    scrollmsg(scroll) {
      this.scroll = scroll;
    },
    tabList() {
      this.list = 1;
      this.me = 0;
    },
    tabMe() {
      this.list = 0;
      this.me = 1;
    }
  }
};
</script>

<style lang="scss">
.store-page {
  width: 100%;
  height: 100%;
  overflow: hidden;
  .store-tab {
    width: 100%;
    display: flex;
    justify-content: space-around;
    border-bottom: 1px solid #ccc;
    span {
      display: inline-block;
      height: 48px;
      line-height: 48px;
    }
    .tab-active {
      color: blue;
    }
  }
  .store-list {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
}
</style>
